header {
  position: fixed;
  padding-block: 20px;
  z-index: 1000;
  bottom: 0px;
  left: 0;
  right: 0;
  background: white;
@include centerChilds;
flex-direction: column;
  p{
    font-size: 2rem;
    margin-block: 5px;
    color: $primary-color;
    text-align: center;
    &:nth-of-type(2){
      font-weight: bold;
    }

    @include md{
      font-size: 1.3rem;
    }
  }
  @include xlg {
    margin-block: 25px;
    padding: 20px min(50px, 5%);
    position: relative;
  }
}

